<template>
    <div class="app-home-subnav">
        <div class="slider-container">
            <ul :style="transformStyle">
                <li v-for="(item, key) in list" :style="item.style">
                    <img :src="item.img">
                    <div>{{item.text}}</div>
                </li>
            </ul>
        </div>
        <button
                @click.stop="throttledArrowClick(activeIndex - 1)"
                class="el-carousel__arrow el-carousel__arrow--left">
            <i class="el-icon-arrow-left"></i>
        </button>
        <button
                @click.stop="throttledArrowClick(activeIndex + 1)"
                class="el-carousel__arrow el-carousel__arrow--right">
            <i class="el-icon-arrow-right"></i>
        </button>
    </div>
</template>
<style lang="less">
    .app-home-subnav{
        color: #333333;
        position:relative;
        height: 9.375rem;
        background-color: rgb(248, 248, 248);
        -webkit-box-shadow: inset 1.5px 2.598px 7px 0px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: inset 1.5px 2.598px 7px 0px rgba(0, 0, 0, 0.15);
        box-shadow: inset 1.5px 2.598px 7px 0px rgba(0, 0, 0, 0.15);
        height: 150px;
        .slider-container {
            width:60%;
            margin:0 auto;
            overflow: hidden;
        }
        ul {
            white-space: nowrap;
            -webkit-transition: transform 0.5s;
            -moz-transition: transform 0.5s;
            -ms-transition: transform 0.5s;
            -o-transition: transform 0.5s;
            transition: transform 0.5s;
        }
        li{
            text-align: center;
            padding-top: 2rem;
            line-height: 2.8rem;
            display: inline-block;
        }
        .el-carousel__arrow{
            background: transparent;
            i{
                color: #333;
            }
        }

    }
</style>
<script>
export default {
  data () {
    return {
      showNum: 3,
      activeIndex: 0,
      transformPerWidth: 0,
      transformX: 0
    }
  },
  computed: {
    transformStyle: function () {
      return {
        msTransform: 'translateX(' + this.transformX + 'px)',
        webkitTransform: 'translateX(' + this.transformX + 'px)',
        transform: 'translateX(' + this.transformX + 'px)'
      }
    }
  },
  props: {
    list: Array
  },
  methods: {
    handleButtonEnter: function () {

    },
    handleButtonLeave: function () {

    },
    throttledArrowClick: function (targetIndex) {
      if (this.list.length <= this.showNum) {
        return
      }
      if (targetIndex < 0) {
        targetIndex = this.list.length - targetIndex + 1
      }
      if (targetIndex + 1 >= this.list.length) {
        targetIndex = targetIndex - this.list.length + 1
      }
      this.activeIndex = targetIndex
      this.transformX = -1 * this.activeIndex * this.transformPerWidth
    }
  },
  mounted: function () {
    var itemWidth = this.transformPerWidth = ~~(this.$el.clientWidth * 0.6 / this.showNum)
    for (var i = 0, total = this.list.length; i < total; i++) {
      this.list[i].style = {
        width: itemWidth + 'px'
      }
      this.$set(this.list, i, this.list[i])
    }
  }
}
</script>



